@import "../styles/App.scss";

.rating {
  position: relative;
  bottom: 70px;
  width: 100%;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.3);

  .rate-box {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    height: 100%;

    .rating-props {
      justify-self: center;
      font-size: 30px;
      margin-top: 10px;

      .rating-props-button {
        color: #98cfa0;
        background: transparent;
        border: none;
        cursor: pointer;
      }

      .rating-props-button-clicked {
        color: #98cfa0;
        background: transparent;
        border: none;
        cursor: pointer;
      }
    }

    .rating-count {
      justify-self: center;
      font-size: 24px;
      line-height: 56px;

      .rate-num {
        color: #ffff;
        font-weight: 700;
      }

      .vote-num {
        opacity: 0;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.8);
        color: #fff;
        text-align: center;
        min-height: 30px;
        padding: 5px 0;
        line-height: 20px;
        bottom: 100%;
        left: 75px;
        width: 50%;
        border-radius: 3px;
        font-size: 12px;
        transition: ease 0.2s all;

        &::before {
          position: absolute;
          content: "";
          display: block;
          left: 50%;
          margin-left: -6px;
          bottom: -12px;
          border: 6px solid transparent;
          border-top-color: rgba(0, 0, 0, 0.8);
        }
      }

      &:hover .vote-num {
        display: block;
        opacity: 1;
      }
    }

    .rating-const {
      justify-self: center;
      margin-top: 10px;
      font-size: 30px;

      .rating-const-button {
        color: $btn-color;
        background: transparent;
        border: none;
        cursor: pointer;
      }

      .rating-const-button-clicked {
        color: $btn-color;
        background: transparent;
        border: none;
        cursor: pointer;
      }
    }
  }
}
