@import "../styles/App.scss";

.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  @include flex-center;
  background-color: rgba(42, 44, 50, 0.75);
  z-index: 200;

  .modal-content {
    width: 45%;
    border-radius: 10px;
    background-color: #fdfdfe;
    overflow-y: auto;

    .modal-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #e5e5e5;
      padding: 25px;
      background-color: $main-color;

      .modal-title {
        color: $white;
        font-size: 1.5rem;
        line-height: 1;
      }

      .modal-close-btn {
        display: inline-block;
        padding: 0 16px;
        text-align: center;
        background-color: transparent;
        border: none;
        font-size: inherit;

        &:before {
          line-height: 1;
          color: $black;
          text-shadow: 0 1px 0 $white;
          content: "\2715";
          opacity: 0.35;
          font-weight: 700;
        }
      }
    }

    .form-body {
      position: relative;
      padding: 20px;
      margin-top: 15px;

      .form-group {
        margin-bottom: 15px;
        flex: 1 0 61%;
        padding: 0 5px;

        .form-label {
          display: inline-block;
          max-width: 100%;
          margin-bottom: 15px;
          margin-left: 0;
          font-size: 1.2rem;
          font-weight: 700;
          color: $text-color;
        }

        .form-field {
          width: 100%;
          height: 52px;
          font-size: 16px;
          padding: 6px 12px;
          background-color: $white;
          color: $text-color;
          border: 1px solid rgba(0, 0, 0, 0.1);
          border-radius: 5px;
          box-shadow: none;
          outline: none;
        }

        .form-field-error {
          border: 1px solid rgb(255, 0, 0);
        }

        .msg-error {
          font-size: 12px;
          color: #7c7979;
          margin-top: 5px;
        }

        .login-submit-btn {
          width: 85%;
          padding: 15px 0;
          margin-left: 7.5%;
          margin-top: 15px;
          text-align: center;
          appearance: none;
          background-color: $main-color;
          color: $white;
          font-weight: 700;
          border: 2px solid $main-color;
          border-radius: 5px;

          &:not(.btn-disabled):hover {
            background-color: $white;
            border: 2px solid $main-color;
            color: $text-color;
          }
        }

        .btn-disabled {
          width: 85%;
          padding: 15px 0;
          margin-left: 7.5%;
          margin-top: 15px;
          text-align: center;
          appearance: none;
          background-color: #bcbdbe;
          color: $white;
          font-weight: 700;
          cursor: not-allowed;
          border: 2px solid #bcbdbe;
          border-radius: 5px;
        }
      }
    }

    .modal-footer {
      background-color: #f3f3f3;
      text-align: center;
      padding: 32px 15px;

      .modal-footer-paragraph {
        font-size: 1rem;
        font-weight: 400;
        line-height: 1.25;

        .signup-footer-link {
          color: $title-color;
          font-weight: 600;
          padding: 0 16px;
          background-color: transparent;
          font-size: inherit;
          line-height: inherit;
          text-decoration: none;
          border: none;
          cursor: pointer;
        }

        .login-footer-btn {
          color: $title-color;
          font-weight: 600;
          padding: 0 16px;
          background-color: transparent;
          font-size: inherit;
          line-height: inherit;
          text-decoration: none;
          border: none;
          cursor: pointer;
        }
      }

      .policy-text {
        margin-bottom: 0;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        font-size: 0.75rem;
        letter-spacing: 0.25px;
        line-height: 1.5rem;
        color: $title-color;

        .small {
          font-weight: 400;
          font-size: 0.75rem;
          padding: 0;
          color: $title-color;
        }
      }
    }
  }

  .height {
    height: auto;
  }
}
