@import "../../styles/App.scss";

.btn {
  @include btn-style;
  transition-duration: 0.4s;

  &:hover {
    background-color: $white;
    border: 2px solid $btn-color;
  }
}

.btn-primary {
  background-color: $btn-color;
  color: $text-color;
}

.btn-link {
  @include flex-center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
}

.btn-back {
  display: inline-block;
  margin-top: 40px;
  @include btn-style;
  background-color: $btn-color;
  color: $text-color;
}
